$mainColor : #ff0000;
@mixin functionnn {
    background-color:#eeeeee;
    padding:20px;
    border-radius:5px;
    max-width:500px;
    width:100%;
    margin:0 auto;
}

.red{
    color:$mainColor;
}

.my-alert{
    @include functionnn()
}

.my-alert-pink{
    @extend .my-alert;
    background-color:pink;
}

.my-alert p{
    margin-bottom:0;
}

